
body{
  margin: 0
}
a {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.carousel-container[data-v-a5d8e1f0] {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  list-style: none;
}
.carousel-container .carousel[data-v-a5d8e1f0] {
  list-style: none;
  margin:0;
  padding: 0;
  overflow:hidden
}
.carousel-container .carousel .carousel-element[data-v-a5d8e1f0]{
  width: 100%;
  float:left;
}
.carousel-container .carousel .img-container[data-v-a5d8e1f0]{
  width: 100%;
}
.carousel-container .carousel .img-container img[data-v-a5d8e1f0]{
  width: 100%;
}

.pagenation-navi{
  width: 100%;
  text-align: center;
  position:absolute;
  bottom: 0;
}
.icon-circle{
  display:inline-block;
  border-radius: 100%;
  width: 10px;
  height: 10px;
  margin-right: 10px;
  background:rgba(255, 255, 255, 0.8);
  -webkit-transition: all ease-in .3s;
  transition: all ease-in .3s;
}
.icon-circle:last-child{
  margin-right: 0;
}
.icon-circle.active{
  background:rgba(0, 0, 0, 0.6);
}

img{
  display:block;
  width: 100%;
  height: 100%;
  border:none
}
